import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import styled from 'styled-components';

import menuNavData from '../data/menuNavData';
import SeoulContainer from '../styles/SeoulContainer';

const MenuNavbar = styled.nav`
  z-index: 1;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 48px;
  /* background: #ffa500; */
  background: ${props =>
    props.backgroundFixed ? 'rgba(0, 0, 0, 0.85)' : props.theme.lightGrey};
  transition: all 300ms;
  display: flex;
  align-items: center;
  * {
    overflow: hidden;
  }
  .slider-wrapper {
    z-index: 2;
    display: flex;
    justify-content: center;
    /* background: black; */
    height: 48px;
    .slider-inner {
      background: ${props => props.theme.lightGrey};
      /* background: yellow; */
      position: relative;
      overflow-x: scroll;
      height: 70px;
      width: 1000px;
      ul.slider {
        /* left: 50%;
        transform: translateX(-50%); */
        display: flex;
        flex-direction: row;
        align-items: center;
        flex-wrap: nowrap;
        /* justify-content: flex-start; */
        position: absolute;

        height: 70px;
        top: -12px;

        li {
          margin-left: 8px;
          border-radius: 4px;
          &:last-child {
            margin-right: 8px;
          }
          a {
            font-family: 'Leckerli One', cursive;
            padding: 8px;
            border-radius: inherit;
            transition: all 300ms ease-in-out;
            white-space: nowrap;
            &.menu-nav-active {
              background: #ffa500;
              color: ${props => props.theme.offWhite};
            }
          }
        }
      }
    }
  }
`;

class MenuNav extends Component {
  constructor() {
    super();
    this.state = {
      fixed: false
    };
    this.menuNav = React.createRef();
  }
  componentDidMount() {
    this.handleMenu();
  }
  static getDerivedStateFromProps(nextProps, prevState) {
    if (nextProps.fixed !== prevState.fixed) {
      return { fixed: nextProps.fixed };
    } else {
      return false;
    }
  }
  handleMenu = () => {
    this.props.menuNavi(this.menuNav.current.offsetTop);
    const sliderWidth = this.menuNav.current.querySelector('.slider')
      .clientWidth;
    console.log(sliderWidth);
    let sliderInner = this.menuNav.current.querySelector('.slider-inner');
    console.log(sliderInner);
    sliderInner.style.width = `${sliderWidth}px`;
  };
  render() {
    const { fixed } = this.props;
    const width = this.props.width;
    const style = {
      width: `${width}px`,
      position: 'absolute',
      bottom: 0,
      left: 0
    };
    const fixedStyle = {
      width: `${width}px`,
      position: 'fixed',
      top: fixed ? this.props.mainNavHeight : '',
      left: 0
    };
    return (
      <MenuNavbar
        className="menuNav"
        style={fixed ? fixedStyle : style}
        ref={this.menuNav}
        backgroundFixed={fixed}
      >
        <SeoulContainer className="slider-wrapper">
          <div className="slider-inner">
            <ul className="slider">
              {menuNavData.map((item, i) => (
                <li key={item.id}>
                  <NavLink to={item.url} activeClassName="menu-nav-active">
                    {item.name}
                  </NavLink>
                </li>
              ))}
            </ul>
          </div>
        </SeoulContainer>
      </MenuNavbar>
    );
  }
}

export default MenuNav;
