import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';

const StyledHeader = styled.header`
  width: 100%;
  min-height: 100vh;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.2)),
    url('/images/landing-header-background.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  div {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0);
    transition: all 1000ms;
    display: flex;
    flex-direction: column;
    align-items: center;
    &.animate {
      opacity: 1;
      transform: translate(-50%, -50%) scale(1);
    }
    * {
      text-align: center;
    }
    h1 {
      font-family: 'Amatic SC', cursive;
      font-size: 100px;
      line-height: 100px;
      color: #fff;
    }
    h2 {
      font-family: 'Amatic SC', cursive;
      font-size: 50px;
      color: #fff;
    }
    h3 {
      font-family: 'Amatic SC', cursive;
      color: #fff;
      font-size: 30px;
      /* line-height: 25px; */
      /* line-height: 60px; */
    }
    a {
      padding: 10px 30px;
      background: ${props => props.theme.red};
      color: #f2f2f2;
      border-radius: 30px;
      outline: none;
    }
  }
`;

class LandingHeader extends Component {
  componentDidMount() {
    const loaded = document.getElementsByClassName('loaded')[0];

    setTimeout(() => {
      loaded.classList.add('animate');
    }, 400);
  }
  render() {
    return (
      <StyledHeader>
        <div className="loaded">
          {/* <h2>Healthy Korean Food</h2> */}
          <h1>Seoul Food Take Out</h1>
          <h3>The Most Authentic Korean Food in Toronto</h3>
          <Link to="/menu">MENU</Link>
        </div>
      </StyledHeader>
    );
  }
}

export default LandingHeader;
