import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import styled, { ThemeProvider } from 'styled-components';
import { TransitionGroup, CSSTransition } from 'react-transition-group';
import $ from 'jquery';

import { theme, GlobalStyle } from './styles/Global';
import Navbar from './components/Navbar';
import Home from './pages/Home';
import Menu from './pages/Menu';
import Catering from './pages/Catering';
import Contact from './pages/Contact';
import ToTop from './components/ToTop';

const Wrapper = styled.div`
  z-index: 1000;
  position: fixed;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  overflow-y: scroll;
`;

// const PageAnimationStyles = styled.div`
//   position: relative;
//   .page-enter {
//     opacity: 1;
//   }

//   .page-enter.page-enter-active {
//     opacity: 1;
//     /* transition: opacity 400ms linear 200ms; */
//     transition: all 400ms;
//   }
//   .page-exit {
//     opacity: 0;
//   }
//   .page-exit.page-exit-active {
//     opacity: 0;
//     /* transition: opacity 200ms linear; */
//     transition: all 400ms;
//   }
// `;

class Pages extends Component {
  constructor() {
    super();
    this.state = {
      toTop: false,
      fixed: false,
      clientWidth: 0,
      menuNavFixed: false,
      mainNavHeight: 0,
      menuNavOffsetTop: 0
    };
    this.wrapper = React.createRef();

    this.handleScroll = this.handleScroll.bind(this);
    // this.scrollToTop = this.scrollToTop.bind(this);
  }

  componentDidMount() {
    window.addEventListener('resize', this.handleResize);
    this.setWidthAndHeight();
  }
  componentWillUnmount() {
    window.removeEventListener('resize', this.handleResize);
  }
  handleResize = () => {
    this.setWidthAndHeight();
  };
  setWidthAndHeight = () => {
    const clientWidth = this.wrapper.current.clientWidth;
    const mainNavHeight = this.wrapper.current.querySelector('.mainNav')
      .clientHeight;
    // const menuNavOffsetTop = this.wrapper.current.querySelector('.menuNav')
    //   .offsetTop;
    // const menuNavFixed = false;
    // console.log('offsetTop', menuNavOffsetTop);
    this.setState({ clientWidth, mainNavHeight });
    // this.menuNavbar(menuNavOffsetTop);
  };
  resetMenu = reset => {
    this.setState({ menuNavFixed: reset });
  };
  handleScroll = () => {
    // if (this.wrapper.current.querySelector('.menuNav')) {
    //   const menuNavOffsetTop = this.wrapper.current.querySelector('.menuNav')
    //     .offsetTop;
    //   this.setState({ menuNavOffsetTop });
    // }
    this.wrapper.current.scrollTop > 0
      ? this.setState({ toTop: true, fixed: true })
      : this.setState({ toTop: false, fixed: false });

    this.wrapper.current.scrollTop >=
    this.state.menuNavOffsetTop - this.state.mainNavHeight
      ? this.setState({ menuNavFixed: true })
      : this.setState({ menuNavFixed: false });
  };
  menuNavbar = menuNavOffsetTop => {
    this.setState({ menuNavOffsetTop });
  };

  scrollToTop = () => {
    // this.wrapper.current.scrollTo({ top: 0, left: 0, behavior: 'smooth' });
    $('.wrapper').animate({ scrollTop: 0 }, 300);
  };
  render() {
    const { clientWidth, fixed, menuNavFixed, mainNavHeight } = this.state;

    return (
      <ThemeProvider theme={theme}>
        <>
          <Wrapper
            className="wrapper"
            ref={this.wrapper}
            onScroll={this.handleScroll}
          >
            <GlobalStyle />
            <Router>
              <Route
                render={({ location }) => (
                  <>
                    <Navbar
                      fixed={fixed}
                      width={clientWidth}
                      location={location}
                      resetMenu={this.resetMenu}
                    />

                    <Switch>
                      <Route
                        exact
                        path="/"
                        render={() => <Home />}
                        // key={location.pathname}
                      />
                      <Route
                        path="/menu"
                        render={() => (
                          <Menu
                            fixed={menuNavFixed}
                            width={clientWidth}
                            menuNavbar={this.menuNavbar}
                            mainNavHeight={mainNavHeight}
                            // key={location.key}
                            // pathname={location.pathname}
                          />
                        )}
                      />
                      <Route
                        path="/catering"
                        render={() => (
                          <Catering
                            width={clientWidth}
                            mainNavHeight={mainNavHeight}
                            // key={location.key}
                            // pathname={location.pathname}
                          />
                        )}
                      />
                      <Route
                        exact
                        path="/contact"
                        render={() => <Contact />}
                        // key={location.pathname}
                      />
                    </Switch>
                  </>
                )}
              />
            </Router>
            <ToTop
              active={
                this.wrapper.current && this.wrapper.current.scrollTop > 0
                  ? true
                  : false
              }
              totoTop={this.scrollToTop}
            />
          </Wrapper>
        </>
      </ThemeProvider>
    );
  }
}

export default Pages;
