import styled from 'styled-components';

const SeoulContainer = styled.div`
  width: 100%;
  margin: 0 auto 0 auto;
  overflow-x: hidden;
  position: relative;
  @media (min-width: 576px) {
    max-width: 540px;
  }
  @media (min-width: 768px) {
    max-width: 720px;
  }
  @media (min-width: 992px) {
    max-width: 960px;
  }
  @media (min-width: 1460px) {
    max-width: 1430px;
  }
`;

export default SeoulContainer;
