import React, { Component } from 'react';

import HeaderStyles from '../styles/HeaderStyles';
import MenuNav from '../components/MenuNav';

class Menu extends Component {
  menuNav = menuNavOffsetTop => {
    this.props.menuNavbar(menuNavOffsetTop);
  };
  render() {
    return (
      <>
        <HeaderStyles background="/images/menu/menu-header-bg2.jpg">
          <MenuNav {...this.props} menuNavi={this.menuNav} />
        </HeaderStyles>
        <div style={{ minHeight: '200vh' }} className="konto" />
      </>
    );
  }
}

export default Menu;
