import styled from 'styled-components';

const HeaderStyles = styled.header(props => ({
  background: `linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.2)), url(${
    props.background
  })`,
  backgroundSize: 'cover',
  width: '100%',
  minHeight: '40vh',
  position: 'relative'
}));

export default HeaderStyles;
