import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import styled from 'styled-components';
import $ from 'jquery';

import SeoulContainer from '../styles/SeoulContainer';

const Navb = styled.nav`
  /* position: fixed; */
  position: ${props => (props.backgroundFixed ? 'fixed' : 'absolute')};
  width: 100%;
  left: 0;
  top: 0;
  z-index: 100;
  /* display: flex;
  justify-content: center; */
  transition: background 0.4s;
  /* background: ${props =>
    props.backgroundFixed ? 'rgba(255, 255, 255, 0.9)' : 'none'}; */
  background: ${props =>
    props.backgroundFixed ? 'rgba(0, 0, 0, 0.85)' : 'none'};
  /* height: calc(80px + 1rem); */
  ul {
    display: flex;
    justify-content: center;
    li {
      padding: 0.5rem 1rem 0.5rem 1rem;
      display: flex;
      justify-content: center;
      align-items: center;
      /* height: calc(80px + 1rem); */
      &:first-child {
        margin-right: 2rem;
      }
      a {
        font-size: 20px;
        text-transform: uppercase;
        /* height: calc(80px); */
        height: 60px;
        display: flex;
        justify-content: center;
        align-items: center;
        /* color: ${props =>
          props.backgroundFixed ? props.theme.black : '#f2f2f2'}; */
        color: ${props =>
          props.backgroundFixed ? props.theme.offWhite : '#f2f2f2'};
        div {
          display: flex;
          align-items: flex-end;
          & > *:nth-child(2) {
            margin-left: 10px;
          }
          img {
            width: auto;
            /* object-fit: cover; */
          }
          img:nth-child(1) {
            height: ${props => (props.mobile === 'mobile' ? '48px' : '60px')};
          }
          img:nth-child(2) {
            height: 38px;
          }
        }
      }
    }
  }
`;

class Navbar extends Component {
  constructor() {
    super();
    this.state = {
      fixed: false
    };
  }
  static getDerivedStateFromProps(nextProps, prevState) {
    if (nextProps.fixed !== prevState.fixed) {
      return { fixed: nextProps.fixed };
    } else {
      return false;
    }
  }
  handleActive = () => {
    this.setState({ active: !this.state.active });
  };
  handleToTop = () => {
    // const wrapper = document.getElementsByClassName('wrapper')[0];
    // wrapper.scrollTo(0, 0);
    $('.wrapper').animate({ scrollTop: 0 });
  };
  render() {
    const { width, location } = this.props;
    const { fixed } = this.state;
    const mobile = width < 768 ? undefined : 'mobile';
    // console.log(width < 768);
    console.log(mobile);
    return (
      <Navb backgroundFixed={fixed} style={{ width }} className="mainNav">
        <SeoulContainer>
          <ul>
            <li>
              <NavLink
                to="/"
                onClick={e => {
                  if (location.pathname === '/') e.preventDefault();
                  else this.handleToTop();
                }}
              >
                {fixed ? (
                  // <img src="/images/horizontal-with-icon-logo.png" alt="" />
                  <div>
                    <img
                      src="/images/logo-white-h60.png"
                      alt=""
                      mobile={mobile}
                    />
                    {mobile && (
                      <img src="/images/horizontal-white-h38.png" alt="" />
                    )}
                  </div>
                ) : (
                  // <img
                  //   src="/images/horizontal-with-icon-white-h80.png"
                  //   alt=""
                  // />
                  <div>
                    <img
                      src="/images/logo-white-h60.png"
                      alt=""
                      mobile={mobile}
                    />
                    {mobile && (
                      <img src="/images/horizontal-white-h38.png" alt="" />
                    )}
                  </div>
                )}
              </NavLink>
            </li>
            <li>
              <NavLink
                to="/menu"
                onClick={e => {
                  if (location.pathname.includes('/menu')) e.preventDefault();
                  else {
                    this.props.resetMenu(false);
                    this.handleToTop();
                  }
                }}
              >
                Menu
              </NavLink>
            </li>
            {/* <li>
              <NavLink
                to="/catering"
                onClick={e => {
                  if (location.pathname.includes('/catering'))
                    e.preventDefault();
                  else {
                    this.props.resetMenu(false);
                    this.handleToTop();
                  }
                }}
              >
                Catering
              </NavLink>
            </li> */}
            <li>
              <NavLink
                to="/contact"
                onClick={e => {
                  if (location.pathname === '/contact') e.preventDefault();
                  else {
                    this.props.resetMenu(false);
                    this.handleToTop();
                  }
                }}
              >
                Contact
              </NavLink>
            </li>
          </ul>
        </SeoulContainer>
      </Navb>
    );
  }
}

export default Navbar;
